/* 背景 */
.part2 .bgimg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.part2{
    font-family: PingFangSC-Regular, sans-serif;
    font-size: 21px;
    color: #fff;
}

/* 上半左边样式 */
.part2 .top-style{
    position: absolute;
    width: 100%;
    height: 50%;
    z-index: 10;
}

.part2 .top-left{
    position: absolute;
    width: 280px;
    height: 280px;
    top: -12%;
    left: -16%;
    z-index: 999;
}

.part2 .top-left .top-touxiang1{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    top: 0;
    left: 0;
    background-color: rgba(13, 29, 54, 0.14);
}

.part2 .top-left .top-touxiang2{
    width: 94%;
    height: 94%;
    border-radius: 50%;
    top: 3%;
    left: 3%;
    position: absolute;
    background-color: rgba(13, 29, 54, 0.14);
}

.part2 .top-left .top-touxiang3{
    position: absolute;
    width: 90%;
    height: 90%;
    top: 5%;
    left: 5%;
    border-radius: 50%;
}

/* 上半部右边样式 */
.part2 .top-right{
    position: absolute;
    width: 57%;
    height: 100%;
    left: 44%;
    top: 0;
}

.part2 .top-right .right1{
    background-color: #5EA47F;
    width: 100%;
    top: 21%;
    position: absolute;
    left: 19%;
    padding: 3% 18%;
    border-radius: 50px 0 0 50px;
}

.part2 .top-right .right2{
    background-color: #5EA47F;
    width: 100%;
    top: 36%;
    position: absolute;
    border-radius: 50px 0 0 50px;
    left: 21%;
    padding: 3% 14%;
}

.part2 .top-right .right3{
    background-color: #5EA47F;
    width: 100%;
    top: 51%;
    position: absolute;
    border-radius: 50px 0 0 50px;
    left: 21%;
    padding: 3% 14%;
}

.part2 .top-right .right4{
    background-color: #5EA47F;
    width: 100%;
    top: 66%;
    position: absolute;
    border-radius: 50px 0 0 50px;
    padding: 3% 0% 3% 35%;
}

/* 下半部样式 */
.part2 .down-style{
    position: absolute;
    top: 42%;
    z-index: 1000;
    width: 100%;
    height: 58%;
}

.part2 .down-style .down-box{
    width: 80%;
    margin: 10%;
    height: 65%;
    background-color: #7FBB9B;
    border-radius: 18px;
    box-shadow:2px 3px 8px #3D9365;
}

.part2 .down-style .down-box .yuan1{
    width: 0.4em;
    height: 0.4em;
    background-color: #3D9365;
    border-radius: 50em;
    position: absolute;
    top: 15%;
    left: 15%;
    box-shadow:1px 1px 3px #14924F;
}

.part2 .down-style .down-box .yuan2{
    width: 0.4em;
    height: 0.4em;
    background-color: #3D9365;
    border-radius: 50em;
    position: absolute;
    top: 15%;
    left: 83%;
    box-shadow:1px 1px 3px #14924F;
}

.part2 .down-style .down-box .yuan3{
    width: 0.4em;
    height: 0.4em;
    background-color: #3D9365;
    border-radius: 50em;
    position: absolute;
    top: 68%;
    left: 15%;
    box-shadow:1px 1px 3px #14924F;
}

.part2 .down-style .down-box .yuan4{
    width: 0.4em;
    height: 0.4em;
    background-color: #3D9365;
    border-radius: 50em;
    position: absolute;
    top: 68%;
    left: 83%;
